body {
  background: #080A29;
  font-family: "Open Sans", sans-serif;
  margin: 0;
  padding: 0;
}

.contenedor {
  width: 98%;
  margin: auto;
}
.contenedor #btMenu {
  display: none;
  right: 0;
  background: none;
  color: #FFF;
  border: none;
  font-size: 1.25em;
}
.contenedor #btSearch {
  display: none;
  left: 0;
  background: none;
  color: #FFF;
  border: none;
  font-size: 1.25em;
}

header {
  padding: 30px 40px;
}
header .contenedor {
  display: flex;
  justify-content: space-between;
}
header .logotipo {
  font-family: "Bebas Neue", cursive;
  font-family: "Otomanopee One", sans-serif;
  color: #fff;
  font-size: 40px;
  text-shadow: 0 -1px 4px #FFF, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px #F00;
}
header .a {
  color: #aaa;
  text-decoration: none;
  margin-right: 20px;
}
header .a:hover {
  color: #fff;
}

footer {
  padding: 30px 40px;
}

