@media screen and (max-width: 500px) {
  .contenedor {
    display: flex;
    justify-content: space-between;
  }
  .contenedor #btSearch {
    display: block;
    cursor: pointer;
    z-index: 2;
  }
}
@media screen and (max-width: 850px) {
  header .contenedor {
    display: flex;
    justify-content: space-between;
  }
  header .contenedor .logotipo {
    font-size: 30px;
  }
  header .contenedor .a {
    position: relative;
    margin-bottom: auto;
  }
  header .contenedor .navMenu {
    display: none;
    flex-direction: column;
    width: 115px;
    height: auto;
    position: absolute;
    top: 80px;
    opacity: 1;
    transition: all 0.5s ease;
    text-align: center;
    padding-left: 10px;
  }
  header .contenedor .navMenu.active {
    display: flex;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    right: 20px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  header .contenedor #btMenu {
    display: block;
    cursor: pointer;
  }
}

